.trainers {
  padding: 0 0 120px 0;
  position: relative;

  @include vp-1200 {
    padding: 0 62px 100px 62px;
  }

  @include vp-768 {
    padding: 0 15px 70px 15px;
  }

  h2 {
    @include reset-margins;
    font: 700 3.07em/1em $ff-titles;
    letter-spacing: 0.03em;
    margin-bottom: 54px;
    text-transform: uppercase;

    @include vp-1200 {
      font: 700 2.63em/1em $ff-titles;
      margin-bottom: 45px;
      text-align: center;
    }

    @include vp-768 {
      font: 700 2em/1em $ff-titles;
    }
  }
}

.trainers > .container {
  @include vp-768 {
    padding: 0 32px;
  }
}

.trainers__list {
  width: 100%;
}

.trainers__wrap {
  &--nojs {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px 10px;

    @include vp-1200 {
      grid-template-columns: 1fr 1fr;
    }

    @include vp-768 {
      grid-template-columns: 1fr;
    }

    div {
      max-width: 100%;
      width: 100%;
      justify-self: center;
    }
  }
}

@keyframes info-slides-up {
  0% {
    position: static;
  }

  100% {
    opacity: 1;
  }
}

.trainers__item {
  color: $color-default-white;
  position: relative;
  min-height: 294px;
  text-align: center;
  overflow: hidden;

  @include vp-768 {
    min-height: 274px;
  }

  &--nojs {
    @include vp-1200 {
      &::before {
        content: "";
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $color-blue;
        opacity: 89%;
      }

      div {
        transform: translateY(0);
      }

      p {
        opacity: 1;
      }

      ul {
        opacity: 1;
      }
    }
  }

  @media (hover: hover),
  screen and (min-width: $vp-1200) {
    &:hover {
      &::before {
        content: "";
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $color-blue;
        opacity: 89%;
      }

      div {
        transform: translateY(0);
      }

      p {
        opacity: 1;
      }

      ul {
        opacity: 1;
      }
    }
  }
}

.trainers__info {
  position: relative;
  padding: 58px 30px;
  height: 100%;
  transform: translateY(65%);
  z-index: 1;
  transition: transform 0.3s ease 0s;

  @include vp-1200 {
    padding: 56px 30px;
  }

  @include vp-768 {
    padding: 45px 25px;
  }

  h3 {
    @include reset-margins;
    font: 700 1.13em/1em $ff-default;
    margin-bottom: 5px;
  }

  p {
    @include reset-margins;
    font: 400 1em/1em $ff-default;
    color: $color-light-white;
    margin-bottom: 38px;
    opacity: 0;
    transition: opacity 0.5s ease 0s;
  }

  ul {
    opacity: 0;
    transition: opacity 0.8s ease 0s;
  }
}

.trainers__image {
  img {
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: $color-arrows;
    object-fit: cover;
    object-position: top center;
  }
}

.trainers__about-list {
  @include reset-margins;
  list-style: none;
  text-align: left;
  padding-left: 12%;

  @include vp-768 {
    padding-left: 5%;
  }

  li {
    font: 400 0.88em/1.28em $ff-default;
    padding-left: 16px;
    position: relative;

    @include vp-768 {
      line-height: 1.35em;
    }

    &:not(:last-of-type) {
      margin-bottom: 16px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 4px;
      left: 0;
      width: 6px;
      height: 6px;
      background-color: $color-default-white;
      border-radius: 50%;
    }
  }
}

.trainers__button {
  &--prev {
    left: 2.2%;

    &::before {
      transform: rotate(-45deg);
    }

    &::after {
      transform: rotate(45deg);
    }

    @include vp-1200 {
      left: 3.8%;
    }

    @include vp-768 {
      left: 5px;
    }
  }

  &--next {
    right: 2.2%;

    @include vp-1200 {
      right: 3.8%;
    }

    @include vp-768 {
      right: 5px;
    }
  }

  &--nojs {
    display: none;
  }
}
